.fx-c {
  display: flex;
  justify-content: center;
  align-items: center;
}
.fx-s {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.fx-st {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.fx {
  display: flex;
}
.al-st {
  align-items: start;
}
.txt-up-cse {
  text-transform: uppercase;
}
.txt-cp {
  text-transform: capitalize;
}

.container {
  max-width: 1400px;
  margin: auto;
}
.mb-1 {
  margin-bottom: 10px;
}
.mb-2 {
  margin-bottom: 20px;
}
.mb-3 {
  margin-bottom: 30px;
}
.mb-4 {
  margin-bottom: 40px;
}
.mb-5 {
  margin-bottom: 50px;
}

.mr-auto {
  margin-right: auto;
}
.ml-auto {
  margin-left: auto;
}
.show {
  display: block;
}
.hide {
  display: none;
}
.ovf-scroll {
  overflow: scroll;
}
.bd-bo-1 {
  border-bottom: 0.5px solid $lightgray;
}

%arc {
  display: inline-block;
  margin-inline: 10px;
  margin-top: 5px;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-top: 8px solid #1348fc;
  transform: rotate(314deg);
}
